import React, { useState } from 'react';
/*import React from 'react'*/
import { navLink } from "../Config/navLinks"


const Navbar = () => {
    const links = navLink.map((value, index) => {
        return (
            <li key={index} className="nav-item">
                <a className="nav-link" href={value.link}>{value.name}</a>
            </li>
        )
    })
    
    window.addEventListener("scroll", () => {
            let nav = document.querySelector('.navbar');
        if (window.scrollY > 150) { nav?.classList.remove('navbar-transparent'); nav?.classList.add('btn-primary'); }
        else { nav?.classList.add('navbar-transparent'); nav?.classList.remove('btn-primary'); }
        console.log(nav);
        });
 
    const [isNavCollapsed, setIsNavCollapsed] = useState(true);

    const handleNavCollapse = () => setIsNavCollapsed(!isNavCollapsed);
   
   
    return (
        <header>
            <div className="profile-page sidebar-collapse">
                <nav className="navbar navbar-expand-lg fixed-top navbar-transparent" >
                    <div className="container">
                        <div className="navbar-translate"><a className="navbar-brand" href="/" rel="tooltip">Creative CV</a>
                            <button className={`navbar-toggler navbar-toggler ${isNavCollapsed ? 'toggled' : 'collapsed'}`} type="button" data-toggle="collapse" data-target="#navigation"
                                aria-controls="navigation" aria-expanded={!isNavCollapsed ? true : false}
                                aria-label="Toggle navigation" onClick={handleNavCollapse}>
                                <span className="navbar-toggler-bar bar1"></span>
                                <span className="navbar-toggler-bar bar2"></span>
                                <span className="navbar-toggler-bar bar3"></span>
                            </button>
                            {/*<button className="navbar-toggler navbar-toggler" onClick={handleNavCollapse}*/}
                            {/*    type="button" data-toggle="collapse" data-target="#navigation"*/}
                            {/*    aria-controls="navigation" aria-expanded={!isNavCollapsed ? true : false}*/}
                            {/*    aria-label="Toggle navigation">*/}
                            {/*    <span className="navbar-toggler-bar bar1"></span>*/}
                            {/*    <span className="navbar-toggler-bar bar2"></span>*/}
                            {/*    <span className="navbar-toggler-bar bar3"></span></button>*/}
                        </div>
                        <div className={`navbar-collapse justify-content-end ${isNavCollapsed ? 'collapse show' : 'collapse'} `} id="navigation">
                            <ul className="navbar-nav">
                                {links}
                            </ul>
                        </div>
                    </div>
                </nav>
            </div>
        </header>
    )
}

export default Navbar
